html {
  box-sizing: border-box;
  font-size: 10px;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  height: inherit;
  margin: 0;
  /* font-family: $base-font-family;
  font-size: $base-font-size;
  line-height: $base-line-height;
  font-weight: $base-font-weight;
  font-style: $base-font-style;
  color: $base-font-color; */ }

img {
  display: block;
  max-width: 100%;
  height: auto; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0; }

.textarea {
  display: block;
  width: 80%;
  max-width: 1000px;
  min-height: 100px;
  margin: 0 auto; }

.keyboard {
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
  background-color: #0479da;
  text-align: center; }

.keyboard__key {
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 3px;
  border: none;
  background-color: tomato;
  font-size: 1rem;
  text-align: center;
  vertical-align: middle;
  color: white; }
  .keyboard__key--active {
    background-color: navy; }
  .keyboard__key--size-1 {
    width: 7%; }
  .keyboard__key--size-2 {
    width: 8%; }
  .keyboard__key--size-3 {
    width: 11%; }
  .keyboard__key--size-4 {
    width: 13%; }
  .keyboard__key--size-5 {
    width: 15%; }
  .keyboard__key--size-6 {
    width: 16%; }
  .keyboard__key--size-7 {
    width: 31%; }
